
































import { Vue, Component } from 'vue-property-decorator';
import CustomizedFooter from 'components/customized-footer.vue';
import CustomizedNav from 'components/customized-nav.vue';

declare type routeType = {
  icon: string,
  path: string,
  label: string
};

const subRoutes: routeType[] = [{
  icon: 'el-icon-document',
  path: '/about/intro',
  label: '公司介绍',
}, {
  icon: 'el-icon-service',
  path: '/about/contact',
  label: '联系我们',
}];

@Component({
  components: {
    CustomizedFooter,
    CustomizedNav,
  },
})
export default class Contact extends Vue {
  currentRoute: string = '';

  activedMenu: string = '/about/intro';

  subRoutes = subRoutes;
  
  syncRoute(source: string) {
    this.currentRoute = source;
    this.activedMenu = this.currentRoute;
  }

  mounted() {
    // init state;
    this.syncRoute(this.$route.path);
  }

  updated() {
    // update state;
    this.syncRoute(this.$route.path);
  }

  toggleActivedMenu(e: string) {
    this.syncRoute(e);
  }
}
